import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { CLASS_NAME, QuadroSocietario } from '../../../../models/dtos/mcg/quadroSocietario';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'socio.nome',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const QuadroSocietarioSearchView: FC<SearchViewProps<QuadroSocietario>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<QuadroSocietario>({ defaultValues: { empresa: { id: globalReducer.globalParameter.empresa.id } }, mode: 'onBlur' });

  function getSearchExpression(): SearchExpression[] {
    const expressions: SearchExpression[] = [];

    if (formProps.getValues().empresa) {
      expressions.push({
        fieldName: 'empresa.id',
        value: formProps.getValues().empresa.id,
      });
    }
    return expressions;
  }

  const columns: ColumnsProps[] = [
    {
      dataField: 'socio.nome',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Sócio',
    },
    {
      dataField: 'percentual',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Percentual',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Quadro de Sócios" wikiId={2127}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        searchExpressions={getSearchExpression}
        extraData={getExtraData(props, 'QuadroSocietarioEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default QuadroSocietarioSearchView;
